import React from 'react';
import {
  Grid,
  Paper,
  Typography,
  Button,
  Input,
  TextField,
  FormControl,
  InputLabel,
  InputAdornment,
} from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import IconButton from '@material-ui/core/IconButton';

import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  sideBarMain: {
    display: 'flex',
    position: 'absolute',
    left: '0px',
    width: '430px',
    height: '100%',
    backgroundColor: '#fff',
  },
  mainBarHeight: {
    height: '250px',
    paddingTop: '60px',
    background: '#000088',
    color: '#fff',
  },
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(3),
      width: '20ch',
    },
    '& .MuiFormControl-root': {
      margin: theme.spacing(3),
      width: '20ch',
    },
  },

  btn: {
    width: '250px',
    borderRadius: '50px',
    marginTop: '20px',
  },
}));

export default function SignUp() {
  const classes = useStyles();
  return (
    <>
      <Grid
        container
        className={classes.sideBarMain}
        direction="column"
        justify="space-between"
        wrap="nowrap"
        spacing={2}
      >
        <Grid item>
          <Paper variant="outlined">
            <Grid container direction="column" justify="center">
              <Grid item className={classes.mainBarHeight}>
                <Typography align="center" variant="h3">
                  Sign Up!
                </Typography>
                <Typography align="center" variant="subtitle1">
                  Please Fill Details Below Fill Your Profile
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        <form className={classes.root} noValidate autoComplete="off">
          <TextField
            required
            id="standard-required"
            label="First Name"
            defaultValue=""
          />
          <TextField
            required
            id="standard-required"
            label="Last Name"
            defaultValue=""
          />
          <Grid item>
            <TextField
              required
              id="standard-required"
              label="Email"
              defaultValue=""
              fullWidth
            />
          </Grid>
          <Grid item>
            <FormControl>
              <InputLabel htmlFor="standard-adornment-password">
                Password
              </InputLabel>
              <Input
                type={'password'}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton>
                      <Visibility />
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
            <FormControl>
              <InputLabel htmlFor="standard-adornment-password">
                Confirm Password
              </InputLabel>
              <Input
                type={'password'}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton>
                      <Visibility />
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
          </Grid>
        </form>
        <Grid item align="center">
          <Button
            variant="contained"
            color="primary"
            href="#contained-buttons"
            fullWidth
            size="large"
            className={classes.btn}
          >
            Submit
          </Button>
        </Grid>
        <Grid item>
          <Paper elevation={0}>
            <Typography variant="subtitle2" color="secondary" align="center">
              By creating the account you agree to our Terms of Service and
              Privacy Policy
            </Typography>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
}
