import React from 'react';
import { Grid, Paper, Typography, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core';
import Input from '@material-ui/core/Input';

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  sideBarMain: {
    display: 'flex',
    position: 'absolute',
    left: '0px',
    width: '430px',
    height: '100%',
    backgroundColor: '#fff',
  },
  maxColumnSize: {
    margin: 'auto',
  },
  flexDirectionRow: {
    display: 'flex',
    flexDirection: 'row',
  },
  inputCountry: {
    width: '30px',
    marginRight: '20px',
  },
  mainBarHeight: {
    height: '300px',
    paddingTop: '60px',
    background: '#000088',
    color: '#fff',
  },
  btn: {
    borderRadius: '50px',
    marginTop: '20px',
  },
  icon: {
    marginRight: '10px',
  },
}));

export default function Verification() {
  const classes = useStyles();
  return (
    <>
      <Grid
        container
        className={classes.sideBarMain}
        direction="column"
        justify="space-between"
        wrap="nowrap"
        spacing={2}
      >
        <Grid item>
          <Paper variant="outlined">
            <Grid container direction="column" justify="center">
              <Grid item className={classes.mainBarHeight}>
                <Typography align="center" variant="h3">
                  Verification
                </Typography>
                <Typography align="center" variant="subtitle1">
                  Please enter 4 Digit Code sent you at 985645654656
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        <Grid item container justify="center">
          <Paper elevation={0}>
            <Grid container className={classes.flexDirectionRow}>
              <Grid item>
                <Input
                  className={classes.inputCountry}
                  placeholder="*"
                  inputProps={{ 'aria-label': 'description' }}
                />
              </Grid>
              <Grid item>
                <Input
                  className={classes.inputCountry}
                  placeholder="*"
                  inputProps={{ 'aria-label': 'description' }}
                />
              </Grid>
              <Grid item>
                <Input
                  className={classes.inputCountry}
                  placeholder="*"
                  inputProps={{ 'aria-label': 'description' }}
                />
              </Grid>
              <Grid item>
                <Input
                  className={classes.inputCountry}
                  placeholder="*"
                  inputProps={{ 'aria-label': 'description' }}
                />
              </Grid>
            </Grid>
            <Grid item>
              <Button
                variant="contained"
                color="primary"
                href="#contained-buttons"
                fullWidth
                size="large"
                className={classes.btn}
              >
                Submit
              </Button>
            </Grid>
          </Paper>
        </Grid>
        <Grid item>
          <Paper elevation={0}>
            <Typography variant="subtitle2" color="secondary" align="center">
              By creating the account you agree to our Terms of Service and
              Privacy Policy
            </Typography>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
}
